import { FC, useEffect, useState } from 'react';
import { Categories, OfferTiles } from '.';
import { IOffer } from '../../api';
import { TextField } from '../../components/core';
import { useTranslations } from '../../hooks/useTranslations';

interface IDomesticTab {
  offers: IOffer[];
}

export const DomesticTab: FC<IDomesticTab> = ({ offers }) => {
  const [filteredOffers, setFilteredOffers] = useState<IOffer[]>();
  const [categories, setCategories] = useState<string[]>();
  const [selectedCategory, setSelectedCategory] = useState('');
  const [term, setTerm] = useState('');
  const { SearchPlaceholder } = useTranslations();

  useEffect(() => {
    getCategories();
    setFilteredOffers(offers);
  }, [offers]);

  useEffect(() => {
    filterOffers();
  }, [selectedCategory, term]);

  const getCategories = () => {
    const _categories: string[] = [];

    offers.forEach(o => {
      if (o.Category && !_categories.includes(o.Category)) {
        _categories.push(o.Category);
      }
    });
    _categories.sort();
    setCategories(_categories);
  };

  const filterOffers = () => {
    setFilteredOffers(
      selectedCategory
        ? offers.filter(o => o.Category === selectedCategory && o.ProductName.toLowerCase().indexOf(term) === 0)
        : offers.filter(o => o.ProductName.toLowerCase().indexOf(term) === 0),
    );
  };

  return (
    <div className="flex flex-col md:flex-row">
      <div className="flex flex-col md:mr-4">
        <div className="mb-4">
          <TextField
            value={term}
            small
            rounded
            placeholder={SearchPlaceholder}
            onChange={e => setTerm(e.currentTarget.value)}
            id="domesticSearchBrand"
          />
        </div>
        <Categories categories={categories || []} selected={selectedCategory} onClick={setSelectedCategory} />
      </div>
      <div className="flex flex-wrap flex-1" id="domesticOffers">
        <OfferTiles offers={filteredOffers || []} />
      </div>
    </div>
  );
};
