import * as ToastPrimitive from '@radix-ui/react-toast';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import { Home, OfferDetails, Receipt } from './containers/Voucher/Pages';
import { VoucherContextProvider } from './context/VoucherContextProvider';

export function App() {
  return (
    <div className="mt-16 px-4 font-inter">
      <BrowserRouter>
        <VoucherContextProvider>
          <ToastPrimitive.Provider>
            <Switch>
              <Route exact path="/voucher/index">
                <Home />
              </Route>
              <Route path="/voucher/offer">
                <OfferDetails />
              </Route>
              <Route path="/voucher/receipt">
                <Receipt />
              </Route>
            </Switch>
          </ToastPrimitive.Provider>
        </VoucherContextProvider>
      </BrowserRouter>
    </div>
  );
}
