import cx from 'classnames';
import React from 'react';

interface ITextField {
  value?: string;
  label?: string;
  small?: boolean;
  rounded?: boolean;
  prefix?: string;
  id?: string;
  invalid?: boolean;
  placeholder?: string;
  onChange?: React.ChangeEventHandler<HTMLInputElement>;
}

export const TextField = React.forwardRef<HTMLInputElement, ITextField>(
  ({ value, label, small, rounded, prefix, id, invalid, placeholder, onChange, ...rest }, ref) => (
    <div className="flex flex-col">
      {label && <label className={cx('text-sm font-bold', invalid ? 'text-br' : 'text-gray-900')}>{label}</label>}
      <div className="flex w-full">
        {prefix && (
          <div
            className={cx(
              'rounded-l px-2 flex items-center justify-center text-base border border-r-0',
              invalid ? 'border-br/40' : 'border-gray-300',
            )}
          >
            {prefix}
          </div>
        )}
        <input
          ref={ref}
          type="text"
          value={value}
          id={id}
          placeholder={placeholder}
          onChange={onChange}
          className={cx(
            'flex items-center justify-center text-base border outline-0 w-full',

            small ? 'py-0.5 px-2' : 'py-2',
            rounded ? 'rounded-full' : '',
            prefix ? 'rounded-r border-l-0 px-2' : small && !rounded ? 'rounded-sm' : 'rounded px-4',
            invalid
              ? 'border-br/40 focus:shadow-focus-invalid focus:border-br/40 active:shadow-focus-invalid active:border-br/40'
              : 'border-gray-300 focus:shadow-focus focus:border-gray-300 active:shadow-focus active:border-gray-300',
          )}
          {...rest}
        />
      </div>
    </div>
  ),
);
