import * as TabsPrimitive from '@radix-ui/react-tabs';
import cx from 'classnames';
import React, { FC } from 'react';

interface ITabs {
  active: string;
  tabs: ITab[];
  onValueChange?: (v: string) => void;
}

interface ITab {
  title: string;
  value: string;
  children: React.ReactNode;
}

export const Tabs: FC<ITabs> = ({ active, tabs, onValueChange }) => (
  <TabsPrimitive.Root defaultValue={active} className="flex flex-col" onValueChange={onValueChange}>
    <TabsPrimitive.List
      className="flex justify-center items-center sm:flex-nowrap flex-wrap p-2 md:p-0 bg-gray-500 md:bg-transparent rounded-lg"
      id="tabs"
    >
      {tabs.map(t => (
        <TabsPrimitive.Trigger
          key={`tab-trigger-${t.title}`}
          value={t.value}
          className={cx(
            'bg-gray-500 relative text-white font-bold outline-0 rounded justify-between flex-1 mx-2',
            'first:ml-0 last:mr-0 text-sm text-center uppercase p-2 cursor-pointer md:hover:bg-white hover:text-br-dark',
            'radix-state-active:bg-white radix-state-active:text-br-dark after:bg-transparent',
            "radix-state-active:after:content-[''] radix-state-active:after:absolute radix-state-active:after:w-full",
            'radix-state-active:after:h-4 radix-state-active:after:-bottom-3 radix-state-active:after:left-0 md:after:bg-white',
          )}
        >
          {t.title}
        </TabsPrimitive.Trigger>
      ))}
    </TabsPrimitive.List>
    {tabs.map(t => (
      <TabsPrimitive.Content key={`tab-content-${t.title}`} value={t.value} className="p-4 bg-white mt-2 rounded">
        {t.children}
      </TabsPrimitive.Content>
    ))}
  </TabsPrimitive.Root>
);
