import { FC } from 'react';
import { Link } from 'react-router-dom';
import { IOffer } from '../../api';

interface IOfferTile {
  offers: IOffer[];
}

export const OfferTiles: FC<IOfferTile> = ({ offers }) => (
  <>
    {offers?.map(o => (
      <div key={o.ProductId} className="xl:basis-1/5 lg:basis-1/4 md:basis-1/3 sm:basis-1/2 basis-full">
        <Link
          to={`/Vouchers/Offer?productId=${o.Carrier}`}
          className="flex rounded-lg border border-gray-300 h-32 mb-4 justify-center items-center sm:mx-2 p-2 hover:shadow-around"
        >
          <img className="max-h-full" src={`/product/image/tile?productId=${o.ProductId}`} alt={o.ProductName} />
        </Link>
      </div>
    ))}
  </>
);
