import cx from 'classnames';
import { forwardRef } from 'react';

interface IDatepicker {
  label?: string;
  minDate?: string;
}

export const Datepicker = forwardRef<HTMLInputElement, IDatepicker>(({ label, minDate, ...rest }, ref) => (
  <div className="flex flex-col w-full">
    {label && <label className="text-sm font-bold text-gray-900">{label}</label>}
    <input
      ref={ref}
      type="date"
      min={minDate}
      className={cx(
        'h-[42px] px-4 text-base border border-gray-300 outline-0',
        'focus:shadow-focus focus:border-gray-300 active:shadow-focus active:border-gray-300 rounded',
      )}
      {...rest}
    />
  </div>
));
