import cx from 'classnames';
import { forwardRef } from 'react';

interface ISelect {
  label?: string;
  options: { label: string; value: string }[];
  invalid?: boolean;
  small?: boolean;
}

export const Select = forwardRef<HTMLSelectElement, ISelect>(({ label, options, invalid, small, ...rest }, ref) => (
  <div className="flex flex-col">
    {label && <label className={cx('text-sm font-bold', invalid ? 'text-br' : 'text-gray-900')}>{label}</label>}
    <select
      ref={ref}
      {...rest}
      className={cx(
        'rounded border border-gray-300 outline-0 bg-white disabled:bg-gray-100',
        invalid
          ? 'border-br/40 focus:shadow-focus-invalid active:shadow-focus-invalid'
          : 'border-gray-300 focus:shadow-focus focus:border-gray-300 active:shadow-focus active:border-gray-300',
        small ? 'py-1 px-2' : 'px-4 h-[42px]',
      )}
    >
      {options.map(o => (
        <option key={o.value} value={o.value}>
          {o.label}
        </option>
      ))}
    </select>
  </div>
));
