import { FC } from 'react';
import { useHistory } from 'react-router-dom';
import { IOfferDetails, IOfferItem } from '../../api';
import { Button, Island } from '../../components/core';
import { useTranslations } from '../../hooks/useTranslations';

interface IOfferDetailsAside {
  offer: IOfferDetails | undefined;
  selectedOfferItem: IOfferItem | undefined;
}

export const OfferDetailsAside: FC<IOfferDetailsAside> = ({ offer, selectedOfferItem }) => {
  const history = useHistory();
  const { OrderSummaryLabel, AmountLabel, LocalAmountLabel, TransactionFeeLabel, NextBtnTitle, CancelBtnTitle } =
    useTranslations();

  return (
    <Island id="offerDetailsAside">
      <div className="flex justify-center items-center rounded-lg overflow-hidden text-center border border-gray-300 p-4 min-h-[180px]">
        {offer && <img src={`/product/image/tile?productId=${offer?.Id}`} alt={offer?.Name} className="max-h-fit" />}
      </div>
      <div
        className="mt-4"
        dangerouslySetInnerHTML={{
          __html: offer?.Description || offer?.Name || '',
        }}
      />
      {selectedOfferItem && (
        <>
          <h4 className="text-base font-bold my-4">{OrderSummaryLabel}</h4>
          <div className="flex justify-between mb-1">
            <span>{AmountLabel}</span>
            <span>${selectedOfferItem.Amount}</span>
          </div>
          {Number(selectedOfferItem.LocalAmount) > 0 && (
            <div className="flex justify-between mb-1">
              <span>{LocalAmountLabel}</span>
              <span>
                {Number(selectedOfferItem.LocalAmount) / 100}
                <span> {selectedOfferItem.LocalCurrency}</span>
              </span>
            </div>
          )}
          {Number(selectedOfferItem.TransactionFee) >= 0 && (
            <div className="flex justify-between">
              <span>{TransactionFeeLabel}</span>
              <span>${selectedOfferItem.TransactionFee}</span>
            </div>
          )}
          <h3 className="font-bold text-2xl flex justify-between mt-4">
            <span>Total</span>
            <span>${Number(selectedOfferItem.Amount) + Number(selectedOfferItem.TransactionFee || 0)}</span>
          </h3>
        </>
      )}
      <hr className="-mx-4 my-6" />
      <Button className="w-full" intent="primary" disabled={!selectedOfferItem} type="submit">
        {NextBtnTitle}
      </Button>
      <Button
        className="w-full mt-4"
        intent="secondary"
        outline
        type="button"
        onClick={() => {
          history.push('/vouchers/index');
        }}
      >
        {CancelBtnTitle}
      </Button>
    </Island>
  );
};
