import { FC, useState } from 'react';
import { MdInfoOutline } from 'react-icons/md';

interface ITerms {
  terms: string | undefined;
}

export const Terms: FC<ITerms> = ({ terms }) => {
  const [termsExpanded, setTermsExpanded] = useState(false);

  return (
    <div className="flex my-4" id="terms">
      {terms && (
        <>
          <div>
            <MdInfoOutline className="text-br" size={24} />
          </div>
          <div className="pl-2 text-xs">
            <span>{terms.split('.')[0]}.</span>
            {termsExpanded && <span>{terms.split('.').slice(1).join('.')}</span>}
            <span
              role="button"
              className="text-focus underline ml-2 hover:text-focus/70"
              onClick={() => setTermsExpanded(!termsExpanded)}
            >
              view {termsExpanded ? 'less' : 'more'}
            </span>
          </div>
        </>
      )}
    </div>
  );
};
