import { FC } from 'react';
import { Link } from 'react-router-dom';
import { IOffer } from '../../api/VoucherApi';

interface IFeatured {
  offers: IOffer[];
}

export const Featured: FC<IFeatured> = ({ offers }) => (
  <div className="flex flex-col md:flex-row">
    <div className="basis-1/2">
      <div className="rounded-lg md:mr-2 overflow-hidden h-full">
        <img
          className="h-auto w-full md:h-full"
          src="https://cdn.bossrevolution.com/Retailers/Content/voucher/voucher-banner.png"
          alt=""
        />
      </div>
    </div>
    <div className="basis-1/2 flex">
      <div className="md:ml-2 flex flex-wrap flex-1 mt-4 md:mt-0 -mb-4 -mx-2 md:mr-0" id="featured">
        {offers.map(o => (
          <Link
            key={o.ProductId}
            to={`/Vouchers/Offer?productId=${o.Carrier}`}
            className="basis-1/2 pb-4 px-2 no-underline"
          >
            <div className="rounded-lg border border-gray-300 flex items-center justify-center p-2 h-full hover:shadow-around cursor-pointer">
              <img src={`/product/image/tile?productId=${o.ProductId}`} alt={o.ProductName} className="max-h-[120px]" />
            </div>
          </Link>
        ))}
      </div>
    </div>
  </div>
);
