export interface IOffer {
  Carrier: string;
  CarrierName: string;
  CountryCode: string;
  CountryName: string;
  ProductId: number;
  ProductName: string;
  RetailerId: number;
  Featured: boolean;
  Category: string;
}

export interface IOfferDetails {
  Id: number;
  Name: string;
  CountryIsoCode: string;
  CountryName: string;
  Image: string;
  OfferItems: IOfferItem[];
  RequiredFields: string[];
  Description: string;
  NumberMask: string;
}

export interface IOfferItem {
  Amount: string;
  LocalAmount: string;
  LocalCurrency: string;
  TransactionFee: number;
  Title: string;
  InventoryItemId: string;
}

export interface IPurchase {
  TransactionId: string;
  LocalAmount: string;
  LocalCurrency: string;
  TransactionStatus: 'Pending' | 'Fail' | 'Success';
  ErrorMessage: string;
}

export interface ITerms {
  Data: string;
}

export class VoucherApi {
  offersUrl: string;
  offerDetailsUrl: string;
  purchaseUrl: string;
  purchaseStateUrl: string;
  termsUrl: string;

  constructor() {
    this.offersUrl = (document.getElementById('offersUrl') as HTMLInputElement)?.value;
    this.offerDetailsUrl = (document.getElementById('offerDetailsUrl') as HTMLInputElement)?.value;
    this.purchaseUrl = (document.getElementById('purchaseUrl') as HTMLInputElement)?.value;
    this.purchaseStateUrl = (document.getElementById('purchaseStateUrl') as HTMLInputElement)?.value;
    this.termsUrl = document.getElementById('termsUrl')
      ? (document.getElementById('termsUrl') as HTMLInputElement)?.value
      : '/api/Vouchers/Terms';
  }

  async getOffers(): Promise<IOffer[] | undefined> {
    try {
      const response = await fetch(this.offersUrl, { cache: 'no-cache' });
      const data = await response.json();
      return data.Data;
    } catch {
      throw new Error('Cannot fetch offers');
    }
  }

  async getOfferDetails(id: string): Promise<IOfferDetails | undefined> {
    try {
      const response = await fetch(`${this.offerDetailsUrl}/${id}`, {
        cache: 'no-cache',
      });
      const data = await response.json();
      return data.IGift || data.DGift || data;
    } catch (error) {
      throw new Error('Cannot fetch offer details');
    }
  }

  async purchase(formData: { [key: string]: string }): Promise<IPurchase | undefined> {
    try {
      const response = await fetch(this.purchaseUrl, {
        method: 'POST',
        cache: 'no-cache',
        body: JSON.stringify(formData),
        headers: {
          'Content-Type': 'application/json',
        },
      });
      const data = await response.json();
      return data;
    } catch (error) {
      throw new Error('Purchase failed');
    }
  }

  async transactionState(
    id: string,
    interval: number,
    maxAttempts: number,
    onError: (e: string) => void,
  ): Promise<IPurchase | undefined> {
    let attempts = 0;

    const executePoll = async (resolve: (response: any) => void) => {
      const result = await fetch(`${this.purchaseStateUrl}/${id}`, {
        cache: 'no-cache',
        headers: {
          'Content-Type': 'application/json',
        },
      }).then(res => {
        if (!res.ok) {
          onError(`${res.status} (${res.statusText})`);
        }

        return res;
      });

      attempts++;

      const response = await result.json();

      if (response.TransactionStatus !== 'Pending' || (maxAttempts && attempts === maxAttempts)) {
        return resolve(response);
      }
      setTimeout(executePoll, interval, resolve);
      return null;
    };

    return new Promise(executePoll);
  }

  async getTerms(): Promise<ITerms | undefined> {
    try {
      const response = await fetch(this.termsUrl, { cache: 'no-cache' });
      const data = await response.json();
      return data;
    } catch (error) {
      throw new Error('Cannot fetch terms and conditions');
    }
  }
}
