import { Transition } from '@headlessui/react';
import * as DialogPrimitive from '@radix-ui/react-dialog';
import cx from 'classnames';
import React, { FC, Fragment, SetStateAction } from 'react';
import { useTranslations } from '../../hooks/useTranslations';
import { Button, IButton } from './Button';

interface IDialog {
  isOpen: boolean;
  setIsOpen: React.Dispatch<SetStateAction<boolean>>;
  title?: string;
  children: React.ReactNode;
  actions?: IButton[] | undefined;
}

export const Dialog: FC<IDialog> = ({ isOpen, setIsOpen, title, children, actions }) => {
  const { CancelBtnTitle } = useTranslations();

  return (
    <DialogPrimitive.Root open={isOpen} onOpenChange={setIsOpen}>
      <Transition.Root show={isOpen}>
        <Transition.Child
          as={Fragment}
          enter="ease-out duration-300"
          enterFrom="opacity-0"
          enterTo="opacity-100"
          leave="ease-in duration-200"
          leaveFrom="opacity-100"
          leaveTo="opacity-0"
        >
          <DialogPrimitive.Overlay forceMount className="fixed inset-0 z-20 bg-black/50" />
        </Transition.Child>
        <Transition.Child
          as={Fragment}
          enter="ease-out duration-300"
          enterFrom="opacity-0 scale-95"
          enterTo="opacity-100 scale-100"
          leave="ease-in duration-200"
          leaveFrom="opacity-100 scale-100"
          leaveTo="opacity-0 scale-95"
        >
          <DialogPrimitive.Content
            forceMount
            className={cx(
              'fixed z-[9999]',
              'w-[95vw] max-w-md rounded-lg p-4 md:w-full',
              'top-[50%] left-[50%] -translate-x-[50%] -translate-y-[50%]',
              'bg-white',
              'focus:outline-none focus-visible:ring-opacity-75',
            )}
          >
            {title && (
              <DialogPrimitive.Title className="text-sm font-medium text-gray-900 dark:text-gray-100">
                {title}
              </DialogPrimitive.Title>
            )}
            <div>{children}</div>
            <div className="pt-8 flex space-x-3 justify-center">
              <>
                {actions && actions.map(a => a)}
                <DialogPrimitive.Close asChild>
                  <Button intent="secondary" outline size="sm">
                    {CancelBtnTitle || 'Cancel'}
                  </Button>
                </DialogPrimitive.Close>
              </>
            </div>
          </DialogPrimitive.Content>
        </Transition.Child>
      </Transition.Root>
    </DialogPrimitive.Root>
  );
};
