import { useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { MdRedeem } from 'react-icons/md';
import uuid from 'react-uuid';
import { Field, OfferDenomination, OfferDetailsAside, OfferDetailsDialog, Terms } from '..';
import { IOfferDetails, IOfferItem, VoucherApi } from '../../../api/VoucherApi';
import { Island, Loading, Select } from '../../../components/core';
import { InputType, IRequiredTypes, RequiredFieldTypes } from '../../../helpers/RequiredFieldsTypes';
import { useQuery } from '../../../hooks/useQuery';
import { useTranslations } from '../../../hooks/useTranslations';
import { PhoneMask } from '../types';

type Inputs = {
  [key: string]: string;
};

const api = new VoucherApi();

export function OfferDetails() {
  const query = useQuery();
  const offerId = query.get('productId') || '';

  const [loading, setLoading] = useState(true);
  const [offer, setOffer] = useState<IOfferDetails>();
  const [selectedOfferItem, setSelectedOfferItem] = useState<IOfferItem>();
  const [isExtended, setIsExtended] = useState<IOfferItem>();
  const [requiredFields, setRequiredFields] = useState<{
    [key: string]: IRequiredTypes[];
  }>();
  const [mask, setMask] = useState<PhoneMask>();
  const [dialogOpen, setDialogOpen] = useState(false);
  const [formData, setFormData] = useState<Inputs>();
  const [terms, setTerms] = useState<string>();
  const {
    GiftInfoTitle,
    CountryLabel,
    RecipientInfoTitle,
    PreferredLanguageLabel,
    EnglishOptionLabel,
    SpanishOptionLabel,
    SenderInfoLabel,
    DeliveryInfoTitle,
  } = useTranslations();

  const {
    handleSubmit,
    register,
    watch,
    formState: { errors },
  } = useForm<Inputs>();

  useEffect(() => {
    api
      .getOfferDetails(offerId)
      .then(response => {
        if (response) {
          if (response.OfferItems && response.OfferItems.length > 1) {
            response.OfferItems.sort((a, b) =>
              // eslint-disable-next-line no-nested-ternary
              a.Amount < b.Amount ? -1 : a.Amount > b.Amount ? 1 : 0,
            );
          }
          setOffer(response);
          setIsExtended(response.OfferItems.find(i => i.Title));
          mapRequiredFields(response);
          setMask({
            code: response.NumberMask ? response.NumberMask.trim().match(/\d/g)?.join('').replace(/^0/, '') || '' : '',
            numberLength: response.NumberMask.match(/X/g)?.length || 0,
            prefix: '+',
          });
        }
        setLoading(false);
      })
      .catch(err => {
        setLoading(false);
        throw new Error(err);
      });
    api
      .getTerms()
      .then(response => {
        if (response) {
          setTerms(response.Data);
        }
      })
      .catch(err => {
        throw new Error(`Terms not found: ${err}`);
      });
  }, [offerId]);

  const mapRequiredFields = (data: IOfferDetails) => {
    // eslint-disable-next-line no-underscore-dangle
    const _requiredFields: { [key: string]: IRequiredTypes[] } = {};

    data.RequiredFields.forEach(i => {
      const sField = i.split('.');
      let formArea = sField[0];

      formArea = formArea === 'billing' ? 'recipient' : formArea;

      if (!_requiredFields[formArea]) {
        _requiredFields[formArea] = [];
      }

      const field = RequiredFieldTypes[i.toLowerCase()];

      if (
        !field ||
        (field.alias && field.type === InputType.email && data.RequiredFields.includes(field.aliasId || ''))
      ) {
        return;
      }

      field.id = i.replace('.', '').toLowerCase();
      field.originalId = i;

      if (!_requiredFields[formArea].includes(field)) {
        _requiredFields[formArea].push(field);

        if (field.confirm) {
          _requiredFields[formArea].push({
            type: InputType.confirm,
            id: field.confirmId,
            label: {
              key: 'ConfirmPhoneNumberLabel',
              default: 'Confirm Phone Number',
            },
          });
        }
      }
    });
    setRequiredFields(_requiredFields);
  };

  const confirmOrder = (data: Inputs) => {
    delete data.confirmrecipientnumber;
    data.requestId = uuid();
    data.inventoryItemId = selectedOfferItem?.InventoryItemId || '';
    if (data.recipientphonenumber) {
      data.recipientphonenumber = `${mask?.code}${data.recipientphonenumber}`;
    }
    if (data.senderphonenumber) {
      data.senderphonenumber = `1${data.senderphonenumber}`;
    }
    setFormData(data);
    setDialogOpen(true);
  };

  return (
    <form onSubmit={handleSubmit(data => confirmOrder(data))}>
      <Loading loading={loading} />
      <div className="flex flex-col md:flex-row">
        <div className="basis-2/3">
          <Island header="E-Gift" className="pb-0" icon={<MdRedeem className="text-br mr-2" size={28} />}>
            <div className="-mx-4 bg-black/[.03] text-lg font-bold px-4 py-2">{GiftInfoTitle}</div>
            <div className={`flex py-4 flex-col md:flex-row ${isExtended ? 'flex-wrap' : ''}`}>
              <div className="md:basis-1/3 pr-4">
                <div className="flex flex-col">
                  <label className="text-sm mb-0.5 font-bold">{CountryLabel}</label>
                  <span className="flex justify-between items-center rounded border border-gray-300 px-2 h-9 bg-black/[.05] cursor-not-allowed">
                    {offer?.CountryName || 'United States'}
                    <span className="text-xs"></span>
                  </span>
                </div>
              </div>
              <OfferDenomination
                offerItems={offer?.OfferItems || []}
                isExtended={isExtended || undefined}
                selectedItem={selectedOfferItem || undefined}
                setSelectedItem={setSelectedOfferItem}
              />
            </div>
            {/* RECIPIENT INFO */}
            {requiredFields && requiredFields.recipient && (
              <>
                <div className="-mx-4 bg-black/[.03] text-lg font-bold px-4 py-2 mb-4">{RecipientInfoTitle}</div>
                <div className="flex flex-col md:flex-row flex-wrap -mx-2">
                  {requiredFields.recipient.map(f => (
                    <div key={f.id} className="md:basis-1/2 xl:basis-1/3 px-2 mb-4">
                      <Field
                        field={f}
                        register={register}
                        mask={mask}
                        watch={watch}
                        invalid={!!errors[f.alias! || f.id!]}
                        carrier={offerId}
                      />
                      {errors[f.alias! || f.id!] && (
                        <p className="text-xs text-br font-medium">{errors[f.alias! || f.id!]?.message}</p>
                      )}
                    </div>
                  ))}
                  <div className="md:basis-1/2 xl:basis-1/3 px-2 mb-4">
                    <Select
                      label={PreferredLanguageLabel}
                      options={[
                        { label: EnglishOptionLabel, value: '0' },
                        { label: SpanishOptionLabel, value: '1' },
                      ]}
                      {...register('NotificationLanguage')}
                    />
                  </div>
                </div>
              </>
            )}
          </Island>
          {/* SENDER/DELIVERY INFO */}
          {requiredFields &&
            Object.keys(requiredFields).map(
              f =>
                !(f === 'recipient') && (
                  <Island className="pb-0" key={f}>
                    <div className="-mx-4 bg-black/[.03] text-lg font-bold px-4 py-2 m-4">
                      {f === 'sender' ? SenderInfoLabel : DeliveryInfoTitle}
                    </div>
                    <div className="flex flex-col md:flex-row flex-wrap -mx-2">
                      {requiredFields[f].map(field => (
                        <div key={field.id} className="md:basis-1/2 xl:basis-1/3 px-2 mb-4">
                          <Field
                            field={field}
                            register={register}
                            invalid={!!errors[field.alias! || field.id!]}
                            mask={{
                              code: '1',
                              numberLength: 10,
                              prefix: '+',
                            }}
                          />
                          {errors[field.alias! || field.id!] && (
                            <p className="text-xs text-br font-medium">{errors[field.alias! || field.id!]?.message}</p>
                          )}
                        </div>
                      ))}
                    </div>
                  </Island>
                ),
            )}
          <Terms terms={terms} />
        </div>
        <div className="basis-1/3 md:pl-4">
          <OfferDetailsAside offer={offer} selectedOfferItem={selectedOfferItem} />
        </div>
      </div>
      <OfferDetailsDialog
        formData={formData}
        isOpen={dialogOpen}
        setIsOpen={setDialogOpen}
        offer={offer}
        selectedOfferItem={selectedOfferItem}
        requiredFields={requiredFields}
        mask={mask}
        carrier={offerId}
      />
    </form>
  );
}
