import { FC } from 'react';
import { InputType, IRequiredTypes } from '../../helpers/RequiredFieldsTypes';
import { useTranslations } from '../../hooks/useTranslations';
import { PhoneMask } from './types';

interface IReceiptField {
  field: IRequiredTypes;
  area: string;
  mask: PhoneMask;
  formData: { [key: string]: string };
  carrier?: string;
}

export const ReceiptFields: FC<IReceiptField> = ({ field, area, mask, formData, carrier }) => {
  const label =
    field.type === InputType.accountId
      ? `${carrier}AccountIdLabel`
      : // eslint-disable-next-line @typescript-eslint/no-non-null-asserted-optional-chain
        field.label?.key!;
  const { [label]: l } = useTranslations();

  return (
    <div
      className={`${field.type === 'confirm' ? 'hidden' : 'flex'} md:flex-col items-start basis-1/3 mb-2 last:mb-0`}
      key={field.id}
    >
      <span className="mr-2 mb-1 md:mb-0 md:mr-0 after:content-[':']">{l || field.label?.default}</span>
      <span>
        {(field.alias && field.alias?.indexOf('phone') > -1) || (field.id && field.id?.indexOf('phone') > -1)
          ? area === 'recipient'
            ? `${mask.prefix}${mask.code} ${formData[field.alias || field.id!].replace(
                new RegExp(`^${mask.code}`),
                '',
              )}`
            : `${formData[field.alias || field.id!].replace(/^1/, '+1 ')}`
          : formData[field.alias || field.id!]}
      </span>
    </div>
  );
};
