import { Dispatch, FC, SetStateAction } from 'react';
import { useTranslations } from '../../hooks/useTranslations';
import { Country } from './InternationalTab';

interface ICountries {
  countries: Country[];
  onClick: Dispatch<SetStateAction<string>>;
}

export const Countries: FC<ICountries> = ({ countries, onClick }) => {
  const { SelectCountryTitle } = useTranslations();

  return (
    <div className="w-full" id="countries">
      <div className="text-center text-lg mb-4">{SelectCountryTitle}</div>
      <div className="flex flex-wrap">
        {countries?.map(c => (
          <span
            key={c.CountryCode}
            className="flex flex-col my-2 mx-4 items-center group"
            role="button"
            onClick={() => onClick(c.CountryCode)}
          >
            <img
              className="w-24 rounded-full overflow-hidden group-hover:shadow-around"
              src={`https://cdn.bossrevolution.com/Content/img/flags-svg/${c.CountryCode.toLowerCase()}.svg`}
              alt={c.CountryName}
            />
            <span className="mt-2 text-sm text-secondary group-hover:underline">{c.CountryName}</span>
          </span>
        ))}
      </div>
    </div>
  );
};
