import * as ToastPrimitive from '@radix-ui/react-toast';
import cx from 'classnames';
import React, { Dispatch, FC, SetStateAction } from 'react';
import { Button } from './Button';

export type ToastIntent = 'success' | 'fail' | 'info';

interface IToast {
  open: boolean;
  setOpen: Dispatch<SetStateAction<boolean>>;
  intent?: ToastIntent;
  title?: string;
  content: string;
  ref?: React.MutableRefObject<null>;
  duration?: number;
}

export const Toast: FC<IToast> = ({ open, setOpen, intent, title, content, duration }) => (
  <>
    <ToastPrimitive.Root
      open={open}
      onOpenChange={setOpen}
      duration={duration}
      className={cx(
        'p-4 z-[10000] fixed bottom-4 inset-x-4 w-auto',
        'md:top-4 md:right-4 md:left-auto md:bottom-auto md:w-full md:max-w-sm',
        'shadow-lg rounded-lg opacity-90',
        'radix-state-open:animate-toast-slide-in-bottom md:radix-state-open:animate-toast-slide-in-right',
        'radix-state-closed:animate-toast-hide',
        'radix-swipe-end:animate-toast-swipe-out',
        'translate-x-radix-toast-swipe-move-x',
        'radix-swipe-cancel:translate-x-0 radix-swipe-cancel:duration-200 radix-swipe-cancel:ease-[ease]',
        intent && intent === 'success' && 'bg-primary text-white',
        intent && intent === 'fail' && 'bg-br text-white',
        (!intent || intent === 'info') && 'bg-focus text-white',
      )}
    >
      {title && <ToastPrimitive.Title className="text-sm font-medium">{title}</ToastPrimitive.Title>}
      <ToastPrimitive.Description className="mt-1 text-sm">{content}</ToastPrimitive.Description>
      <ToastPrimitive.Close asChild>
        <Button className="rounded-full w-4 h-4 flex items-center justify-center text-lg !text-white absolute right-2 top-2 min-w-0 bg-transparent">
          <span></span>
        </Button>
      </ToastPrimitive.Close>
    </ToastPrimitive.Root>
    <ToastPrimitive.Viewport />
  </>
);
