import { useContext, useEffect, useState } from 'react';
import { MdRedeem } from 'react-icons/md';
import { DomesticTab, Featured, InternationalTab } from '..';
import { IOffer, VoucherApi } from '../../../api/VoucherApi';
import { Island, Loading, Tabs } from '../../../components/core';
import { VoucherContext } from '../../../context/VoucherContextProvider';
import { useTranslations } from '../../../hooks/useTranslations';

const api = new VoucherApi();

export function Home() {
  const [offers, setOffers] = useState<IOffer[] | null>();
  const [featured, setFeatured] = useState<IOffer[] | null>();
  const [loading, setLoading] = useState(true);
  const { TabUsTitle, TabInternationalTitle } = useTranslations();
  const { state, dispatch } = useContext(VoucherContext);

  useEffect(() => {
    api
      .getOffers()
      .then(response => {
        if (response) {
          setOffers(
            response.sort((a, b) => (a.CarrierName < b.CarrierName ? -1 : a.CarrierName > b.CarrierName ? 1 : 0)),
          );
          getFeatured(response);
        }
        setLoading(false);
      })
      .catch(err => {
        setLoading(false);
        throw new Error(err);
      });
  }, []);

  const getFeatured = (response: IOffer[]) => {
    // eslint-disable-next-line no-underscore-dangle
    const _featured: IOffer[] = [];

    response.forEach(r => {
      if (_featured.length < 4 && r.Featured) {
        _featured.push(r);
      }
    });

    setFeatured(_featured);
  };

  return (
    <>
      <Loading loading={loading} />
      <Island className="mb-4" header="E-Gift" icon={<MdRedeem className="text-br mr-2" size={28} />}>
        <Featured offers={featured || []} />
      </Island>
      <Tabs
        active={state.area}
        onValueChange={v => {
          dispatch?.({ type: 'changeArea', payload: v });
          dispatch?.({ type: 'changeCountry', payload: '' });
        }}
        tabs={[
          {
            title: TabUsTitle,
            value: 'domestic',
            children: <DomesticTab offers={offers?.filter(o => o.CountryCode.toLowerCase() === 'us') || []} />,
          },
          {
            title: TabInternationalTitle,
            value: 'international',
            children: <InternationalTab offers={offers?.filter(o => o.CountryCode.toLowerCase() !== 'us') || []} />,
          },
        ]}
      />
    </>
  );
}
