import cx from 'classnames';
import React, { FC } from 'react';
import { IOfferItem } from '../../api';

interface IOfferDenomination {
  offerItems: IOfferItem[];
  selectedItem: IOfferItem | undefined;
  isExtended: IOfferItem | undefined;
  setSelectedItem: React.Dispatch<React.SetStateAction<IOfferItem | undefined>>;
}

export const OfferDenomination: FC<IOfferDenomination> = ({
  offerItems,
  selectedItem,
  isExtended,
  setSelectedItem,
}) => (
  <div
    id="denomination"
    className={cx(
      'flex',
      isExtended ? 'basis-full mt-4 -mx-2 flex-wrap' : 'md:basis-2/3 md:pl-6 -mx-2 md:mx-0 mt-4 md:mt-0',
    )}
  >
    {offerItems.map(i => (
      <div key={i.InventoryItemId} className={cx(isExtended ? 'basis-1/2 md:basis-1/3 px-2 mb-4' : '')}>
        <span
          role="button"
          onClick={() => setSelectedItem(i)}
          className={cx(
            'flex items-center font-bold border border-gray-300 hover:shadow-around transition-shadow shadow-none',
            isExtended
              ? 'rounded-lg text-4xl text-gray-900 flex-col justify-end py-4 h-full'
              : 'justify-center text-primary rounded-2xl text-lg h-14 w-14 mx-2',
            selectedItem && selectedItem.InventoryItemId === i.InventoryItemId
              ? 'shadow-inset-primary border-primary-light hover:shadow-inset-primary'
              : '',
          )}
        >
          {isExtended && <span className="capitalize text-sm font-normal mb-2 text-center">{i.Title}</span>}${i.Amount}
        </span>
      </div>
    ))}
  </div>
);
