import React, { createContext, FC, useMemo, useReducer } from 'react';

interface IVoucherContextProvider {
  children: React.ReactNode;
}

interface Action {
  type: string;
  payload: string;
}

interface IVoucherContext {
  language: string;
  area: string;
  country: string;
}

interface Store {
  state: IVoucherContext;
  dispatch?: React.Dispatch<any>;
}

const initialState = {
  language:
    (document.getElementById('selectedLanguage') &&
      (document.getElementById('selectedLanguage') as HTMLInputElement).value) ||
    'en',
  area: 'domestic',
  country: '',
};

export const VoucherContext = createContext<Store>({
  state: initialState,
  // eslint-disable-next-line @typescript-eslint/no-empty-function
  dispatch: () => {},
});

function reducer(state: IVoucherContext, action: Action) {
  switch (action.type) {
    case 'changeArea':
      return { ...state, area: action.payload };
    case 'changeCountry':
      return { ...state, country: action.payload };
    default:
      throw new Error('Action not recognized');
  }
}

export const VoucherContextProvider: FC<IVoucherContextProvider> = ({ children }) => {
  const [state, dispatch] = useReducer(reducer, initialState);

  return (
    <VoucherContext.Provider value={useMemo(() => ({ state, dispatch }), [state])}>{children}</VoucherContext.Provider>
  );
};
