import { FC, useContext } from 'react';
import { FieldValues, UseFormRegister, UseFormWatch } from 'react-hook-form';
import { Datepicker, Select, TextField } from '../../components/core';
import { VoucherContext } from '../../context/VoucherContextProvider';
import { InputType, IRequiredTypes } from '../../helpers/RequiredFieldsTypes';
import { useTranslations } from '../../hooks/useTranslations';
import { PhoneMask } from './types';

interface IField {
  field: IRequiredTypes;
  register?: UseFormRegister<FieldValues>;
  watch?: UseFormWatch<{ [key: string]: string }>;
  mask?: PhoneMask;
  invalid?: boolean;
  carrier?: string;
}

export const Field: FC<IField> = ({ field, register, watch, mask, invalid, carrier }) => {
  let today = '';
  let validation = {};
  const { state } = useContext(VoucherContext);
  const label = field.type === InputType.accountId ? `${carrier}AccountIdLabel` : field.label?.key!;

  const { [label]: l } = useTranslations();

  if (field.type === InputType.phone) {
    validation = {
      pattern: {
        value: /\d/g,
        message: 'Incorrent number',
      },
      minLength: {
        value: mask?.numberLength,
        message: `${l || field.label?.default} must be ${mask?.numberLength} digits`,
      },
      maxLength: {
        value: mask?.numberLength,
        message: `${l || field.label?.default} must be ${mask?.numberLength} digits`,
      },
    };
  }

  if (field.type === InputType.text && field.id!.toLowerCase().indexOf('name') > 0) {
    const amount = field!.id!.toLowerCase().indexOf('firstname') > 0 ? 10 : 14;

    validation = {
      maxLength: {
        value: amount,
        message: `${l || field.label?.default} should be at most ${amount} characters`,
      },
      pattern: {
        value: /^[a-zA-Z]+[ \-']?[a-zA-Z]{1,}$/g,
        message: `${l || field.label?.default} is incorrect`,
      },
    };
  }

  if (field.type === 'date') {
    const now = new Date();
    today = `${now.getFullYear()}-${`0${now.getMonth() + 1}`.slice(-2)}-${`0${now.getDate()}`.slice(-2)}`;
  }

  return (
    <>
      {/* TEXT */}
      {field.type === InputType.text && (
        <TextField
          label={l || field.label?.default}
          invalid={invalid}
          {...register!(field.alias! || field.id!, {
            required: `${l || field.label?.default} is required`,
            ...validation,
          })}
        />
      )}
      {/* PHONE */}
      {field.type === InputType.phone && (
        <TextField
          label={l || field.label?.default}
          prefix={`${mask?.prefix}${mask?.code}`}
          invalid={invalid}
          {...register!(field.alias! || field.id!, {
            required: `${l || field.label?.default} is required`,
            ...validation,
          })}
        />
      )}
      {/* CONFIRM PHONE */}
      {field.type === InputType.confirm && (
        <TextField
          label={l || field.label?.default}
          prefix={`${mask?.prefix}${mask?.code}`}
          invalid={invalid}
          {...register!(field.alias! || field.id!, {
            required: `${l || field.label?.default} is required`,
            // eslint-disable-next-line consistent-return
            validate: value => {
              if (watch!('recipientphonenumber') !== value) {
                return 'Phone numbers do not match';
              }
            },
          })}
        />
      )}
      {/* DROPDOWN */}
      {field.type === InputType.dropdown && (
        <Select
          label={l || field.label?.default}
          options={
            field.options?.map(o => ({
              label: o[state?.language || 'en'].label,
              value: o[state?.language || 'en'].value,
            })) || []
          }
          {...register!(field.alias! || field.id!, {
            required: `${l || field.label?.default} is required`,
            ...validation,
          })}
        />
      )}
      {/* DATE */}
      {field.type === InputType.date && (
        <Datepicker
          label={l || field.label?.default}
          minDate={today}
          max=""
          {...register!(field.alias! || field.id!, {
            required: `${l || field.label?.default} is required`,
            value: today,
          })}
        />
      )}
      {/* EMAIL */}
      {field.type === InputType.email && (
        <TextField
          label={l || field.label?.default}
          invalid={invalid}
          {...register!(field.alias! || field.id!, {
            required: `${l || field.label?.default} is required`,
            pattern: {
              value: /^[\w-.]+@([\w-]+\.)+[\w-]{2,4}$/g,
              message: 'Invalid email address',
            },
          })}
        />
      )}
      {/* BILLING ACCOUNT ID */}
      {field.type === InputType.accountId && (
        <TextField
          label={l || field.label?.default}
          invalid={invalid}
          {...register!(field.alias! || field.id!, {
            required: `${l || field.label?.default} is required`,
            ...validation,
          })}
        />
      )}
    </>
  );
};
