import React, { FC } from 'react';

interface IIsland {
  children: React.ReactNode;
  className?: string;
  header?: string;
  icon?: React.ReactNode;
  id?: string;
}

export const Island: FC<IIsland> = ({ children, className, icon, header, id }) => (
  <div id={id} className={`bg-white rounded-lg p-4 mb-4 ${className || ''}`}>
    {header && (
      <h3 className="text-2xl font-bold mb-4 flex items-center">
        {icon && <span>{icon}</span>}
        <span>{header}</span>
      </h3>
    )}
    {children}
  </div>
);
