import React, { FC, SetStateAction, useState } from 'react';
import { useHistory } from 'react-router-dom';
import { IOfferDetails, IOfferItem, VoucherApi } from '../../api/VoucherApi';
import { Button, Dialog, Toast } from '../../components/core';
import { IRequiredTypes } from '../../helpers/RequiredFieldsTypes';
import { useTranslations } from '../../hooks/useTranslations';
import { PhoneMask, ToastMessage } from './types';

interface IOfferDetailsDialog {
  offer: IOfferDetails | undefined;
  selectedOfferItem: IOfferItem | undefined;
  isOpen: boolean;
  setIsOpen: React.Dispatch<SetStateAction<boolean>>;
  formData: { [key: string]: string } | undefined;
  requiredFields: { [key: string]: IRequiredTypes[] } | undefined;
  mask?: PhoneMask;
  carrier?: string;
}

const api = new VoucherApi();
const defaultToastMessage: ToastMessage = {
  title: 'Fail',
  content: 'An error has occured',
  intent: 'fail',
};

export const OfferDetailsDialog: FC<IOfferDetailsDialog> = ({
  offer,
  selectedOfferItem,
  isOpen,
  setIsOpen,
  formData,
  requiredFields,
  mask,
  carrier,
}) => {
  const [processing, setProcessing] = useState(false);
  const [toastVisible, setToastVisible] = useState(false);
  const [toastMessage, setToastMessage] = useState<ToastMessage>(defaultToastMessage);
  const { TotalAmountLabel, SubmitBtnTitle, RecipientNumberLabel } = useTranslations();

  const history = useHistory();

  const purchase = () => {
    setProcessing(true);
    api
      .purchase(formData!)
      .then(response => {
        if (response) {
          if (!response.TransactionStatus || response.TransactionStatus === 'Fail') {
            setToastMessage({
              title: 'Fail',
              content: response.ErrorMessage || 'An error has occurred',
              intent: 'fail',
            });
            setToastVisible(true);
            setProcessing(false);
            return;
          }
          api.transactionState(response.TransactionId || '', 5000, 6, handleStatusError).then(stateResponse => {
            if (!stateResponse || stateResponse.TransactionStatus === 'Fail') {
              setToastMessage({
                ...toastMessage,
                content: stateResponse?.ErrorMessage || 'An error has occurred',
              });
              setToastVisible(true);
              setProcessing(false);
            } else {
              setProcessing(false);
              history.push('/vouchers/receipt', {
                formData,
                selectedOfferItem,
                transactionStatus: stateResponse,
                offer,
                requiredFields,
                mask,
                carrier,
              });
            }
          });
        }
      })
      .catch(err => {
        setToastMessage({
          ...toastMessage,
          content: err,
        });
        setToastVisible(true);
        setProcessing(false);
      });
  };

  const handleStatusError = (error: string) => {
    setToastMessage({
      ...toastMessage,
      content: `Status code error: ${error}`,
    });

    setToastVisible(true);
  };

  return (
    <>
      <Dialog
        isOpen={isOpen}
        setIsOpen={setIsOpen}
        actions={[
          <Button intent="primary" size="sm" processing={processing} onClick={purchase} key="submit">
            {SubmitBtnTitle}
          </Button>,
        ]}
      >
        <>
          <div className="text-center">
            <div className="inline-block border border-gray-300 h-24 rounded p-0.5 mb-2 w-auto">
              <img src={`/product/image/tile?productId=${offer?.Id}`} alt={offer?.Name} className="max-h-full" />
            </div>
            {formData?.recipientphonenumber && (
              <>
                <div className="mb-1">
                  <div className="text-sm">{RecipientNumberLabel}</div>
                  <div className="text-2xl font-bold">
                    {`${mask?.prefix}${mask?.code} 
                      ${formData.recipientphonenumber.replace(new RegExp(`^${mask?.code}`), '')}
                    `}
                  </div>
                </div>
                <div>
                  <div className="text-sm">{TotalAmountLabel}</div>
                  <div className="text-2xl font-bold">
                    ${Number(selectedOfferItem?.Amount) + Number(selectedOfferItem?.TransactionFee || 0)}
                  </div>
                </div>
              </>
            )}
          </div>
        </>
      </Dialog>
      <Toast
        content={toastMessage?.content || ''}
        open={toastVisible}
        setOpen={setToastVisible}
        title={toastMessage?.title}
        intent={toastMessage?.intent || 'info'}
      />
    </>
  );
};
