import cx from 'classnames';
import { Dispatch, FC, SetStateAction } from 'react';

interface ICategories {
  categories: string[];
  selected: string;
  onClick: Dispatch<SetStateAction<string>>;
  disabled?: boolean;
}

export const Categories: FC<ICategories> = ({ categories, selected, onClick, disabled }) => (
  <div className="flex flex-col text-sm" id="categories">
    <span
      role="button"
      className={cx(
        'flex mb-4 hover:underline',
        !disabled && selected === '' ? 'text-secondary underline' : 'text-primary',
        disabled && 'pointer-events-none text-gray-300',
      )}
      onClick={e => onClick(e.currentTarget.dataset.category || '')}
    >
      All Gifts
    </span>
    {categories.map(c => (
      <span
        key={c}
        role="button"
        className={cx(
          'flex mb-4 hover:underline',
          !disabled && selected === c ? 'text-secondary underline' : 'text-primary',
          disabled && 'pointer-events-none text-gray-300',
        )}
        onClick={e => onClick(e.currentTarget.dataset.category || '')}
        data-category={c}
      >
        {c}
      </span>
    ))}
  </div>
);
