import cx from 'classnames';
import React from 'react';

export interface IButton {
  size?: 'sm' | 'lg';
  square?: boolean;
  className?: string;
  outline?: boolean;
  onClick?: () => void;
  children?: React.ReactNode;
  intent?: 'primary' | 'secondary';
  disabled?: boolean;
  processing?: boolean;
  type?: 'button' | 'reset' | 'submit';
  id?: string;
}

export const Button = React.forwardRef<HTMLButtonElement, IButton>(
  ({ size, square, className, outline, onClick, children, intent, disabled, processing, type, id }, ref) => (
    <button
      ref={ref}
      onClick={onClick}
      // eslint-disable-next-line react/button-has-type
      type={type}
      id={id}
      disabled={disabled}
      className={cx(
        'inline-flex justify-center items-center rounded px-4 outline-0 font-bold border border-transparent uppercase text-sm',
        intent
          ? outline
            ? `bg-white border-${intent} hover:bg-black/[.05] text-${intent}`
            : `bg-${intent} hover:bg-${intent}/90 focus:bg-${intent}/90 text-white`
          : 'bg-gray-300 text-gray-600',
        size ? (size === 'sm' ? 'py-1' : 'py-3.5') : 'py-2.5',
        !square && 'min-w-[120px]',
        disabled && '!cursor-not-allowed !bg-gray-100 !border-gray-300 !text-gray-300',
        processing && 'opacity-70 cursor-not-allowed pointer-events-none',
        className,
      )}
    >
      {processing && (
        <svg
          className="animate-spin -ml-1 mr-3 h-5 w-5 text-white"
          xmlns="http://www.w3.org/2000/svg"
          fill="none"
          viewBox="0 0 24 24"
        >
          <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4" />
          <path
            className="opacity-75"
            fill="currentColor"
            d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
          />
        </svg>
      )}
      {children}
    </button>
  ),
);

