import { FC, useContext, useEffect, useState } from 'react';
import { MdSyncAlt } from 'react-icons/md';
import { Categories, Countries, OfferTiles } from '.';
import { IOffer } from '../../api';
import { Button, TextField } from '../../components/core';
import { VoucherContext } from '../../context/VoucherContextProvider';
import { useTranslations } from '../../hooks/useTranslations';

interface IInternationalTab {
  offers: IOffer[];
}

export type Country = { CountryName: string; CountryCode: string };

export const InternationalTab: FC<IInternationalTab> = ({ offers }) => {
  const [filteredOffers, setFilteredOffers] = useState<IOffer[]>();
  const [categories, setCategories] = useState<string[]>();
  const [selectedCategory, setSelectedCategory] = useState('');
  const [term, setTerm] = useState('');
  const [selectedCountry, setSelectedCountry] = useState('');
  const [countryTerm, setCountryTerm] = useState('');
  const [countries, setCountries] = useState<Country[]>();
  const [filteredCountries, setFilteredCountries] = useState<Country[]>();
  const [countryCategories, setCountryCategories] = useState<string[]>();
  const { CountryPlaceholder, SearchPlaceholder } = useTranslations();
  const { state, dispatch } = useContext(VoucherContext);

  useEffect(() => {
    getCategoriesAndCountries();
    setFilteredOffers(offers);
  }, [offers]);

  useEffect(() => {
    filterOffers();
  }, [selectedCategory, term, selectedCountry]);

  useEffect(() => {
    if (!countries) {
      return;
    }
    filterCountriesByTerm();
  }, [countryTerm]);

  useEffect(() => {
    if (!selectedCountry && !filteredOffers) {
      return;
    }

    if (filteredOffers && filteredOffers.length > 0 && filteredOffers.length === offers.length && state.country) {
      setSelectedCountry(state.country);
      return;
    }

    const _categories: string[] = [];

    filteredOffers?.forEach(o => {
      if (o.CountryCode === selectedCountry && !_categories?.includes(o.Category)) {
        _categories.push(o.Category);
      }
    });

    setCountryCategories(_categories);
  }, [filteredOffers]);

  const filterCountriesByTerm = () => {
    setFilteredCountries(countries?.filter(c => c.CountryName.toLowerCase().indexOf(countryTerm.toLowerCase()) === 0));
  };

  const getCategoriesAndCountries = () => {
    const _categories: string[] = [];
    const _countries: Country[] = [];

    offers.forEach(o => {
      if (o.Category && !_categories.includes(o.Category)) {
        _categories.push(o.Category);
      }

      if (o.Category && !_countries.find(c => c.CountryCode === o.CountryCode)) {
        _countries.push({
          CountryCode: o.CountryCode,
          CountryName: o.CountryName,
        });
      }
    });
    _categories.sort();
    _countries.sort((a, b) => (a.CountryName < b.CountryName ? -1 : a.CountryName > b.CountryName ? 1 : 0));
    setCategories(_categories);
    setCountries(_countries);
    setFilteredCountries(_countries);
  };

  const filterOffers = () => {
    setFilteredOffers(
      selectedCategory
        ? offers.filter(
            o =>
              o.Category === selectedCategory &&
              o.ProductName.toLowerCase().indexOf(term) === 0 &&
              o.CountryCode === selectedCountry,
          )
        : offers.filter(o => o.ProductName.toLowerCase().indexOf(term) === 0 && o.CountryCode === selectedCountry),
    );
  };

  return (
    <div className="flex flex-col md:flex-row">
      <div className="flex flex-col md:mr-4">
        {/* SEARCH BY COUNTRY */}
        {!selectedCountry && (
          <div className="mb-4">
            <TextField
              value={countryTerm}
              small
              rounded
              placeholder={CountryPlaceholder}
              onChange={e => setCountryTerm(e.currentTarget.value)}
              id="searchCountry"
            />
          </div>
        )}
        {selectedCountry.length > 0 && (
          <>
            <div className="flex justify-between mb-4">
              <div className="flex items-center">
                <img
                  src={`https://cdn.bossrevolution.com/Content/img/flags-svg/${selectedCountry.toLowerCase()}.svg`}
                  alt={selectedCountry}
                  className="w-5 mr-2 rounded-full"
                />
                <span>{countries?.find(c => c.CountryCode === selectedCountry)?.CountryName}</span>
              </div>
              {/* BACK TO COUNTRIES */}
              <Button
                size="sm"
                intent="primary"
                outline
                square
                className="w-6 h-6 min-w-0 justify-center px-0"
                onClick={() => {
                  setSelectedCountry('');
                  setSelectedCategory('');
                  setCountryTerm('');
                  setTerm('');
                  if (dispatch) {
                    dispatch({ type: 'changeCountry', payload: '' });
                  }
                }}
                id="changeCountry"
              >
                <MdSyncAlt size={16} />
              </Button>
            </div>
            {/* SEARCH BY BRAND */}
            <div className="mb-4">
              <TextField
                value={term}
                small
                rounded
                placeholder={SearchPlaceholder}
                onChange={e => setTerm(e.currentTarget.value)}
                id="intlSearchBrand"
              />
            </div>
          </>
        )}
        <Categories
          categories={(selectedCountry ? countryCategories : categories) || []}
          selected={selectedCategory}
          onClick={setSelectedCategory}
          disabled={!selectedCountry}
        />
      </div>
      <div className="flex flex-wrap flex-1" id="intlOffer">
        {!selectedCountry && (
          <Countries
            countries={filteredCountries || []}
            onClick={v => {
              if (dispatch) {
                dispatch({ type: 'changeCountry', payload: v });
              }
              setSelectedCountry(v);
            }}
          />
        )}
        {selectedCountry.length > 0 && <OfferTiles offers={filteredOffers || []} />}
      </div>
    </div>
  );
};
