import { useEffect, useRef, useState } from 'react';
import { MdRedeem } from 'react-icons/md';
import { Link, useHistory, useLocation } from 'react-router-dom';
import { useReactToPrint } from 'react-to-print';
import { IOfferDetails, IOfferItem, IPurchase } from '../../../api';
import { Button, Island, Toast } from '../../../components/core';
import { IRequiredTypes } from '../../../helpers/RequiredFieldsTypes';
import { useTranslations } from '../../../hooks/useTranslations';
import { ReceiptFields } from '../ReceiptFields';
import { PhoneMask, ToastMessage } from '../types';

type ReceiptType = {
  formData: { [key: string]: string };
  offer: IOfferDetails;
  selectedOfferItem: IOfferItem;
  transactionStatus: IPurchase;
  requiredFields: { [key: string]: IRequiredTypes[] };
  mask: PhoneMask;
  carrier?: string;
};

export function Receipt() {
  const location = useLocation();
  const state = location.state as ReceiptType;
  const history = useHistory();
  const [toastVisible, setToastVisible] = useState(false);
  const {
    OrderSuccessfulMessage,
    ToastSuccessfulMessage,
    StartNewOrderBtn,
    PrintBtnTitle,
    SmallPrintBtnTitle,
    RecipientInfoTitle,
    SenderInfoLabel,
    DeliveryInfoTitle,
    AmountLabel,
    LocalAmountLabel,
    TransactionFeeLabel,
  } = useTranslations();

  const defaultToastMessage: ToastMessage = {
    title: 'Success',
    content: ToastSuccessfulMessage,
    intent: 'success',
    duration: 10000,
  };

  const [toastMessage, setToastMessage] = useState<ToastMessage>(defaultToastMessage);

  const printRef = useRef(null);

  useEffect(() => {
    if (state.transactionStatus.TransactionStatus !== 'Success') {
      setToastMessage({
        title: 'Note',
        content:
          'We are currently experiencing high volume of transactions; your request is being processed.' +
          ' Please allow up to 2 hours for the transaction to complete.',
        intent: 'info',
        duration: 30000,
      });
    }
    setToastVisible(true);

    const unblock = history.block((l, action) => {
      if (l.key) {
        return;
      }

      // eslint-disable-next-line consistent-return
      return false;
    });
  }, []);

  const handlePrint = useReactToPrint({
    content: () => printRef.current,
  });

  return (
    <>
      <Island header="E-Gift" icon={<MdRedeem className="text-br mr-2" size={28} />}>
        <div className="bg-primary/10 p-4 -mx-4 flex items-center justify-center text-lg text-primary font-bold my-6">
          {OrderSuccessfulMessage}
        </div>
        <div className="text-center print:p-4" ref={printRef}>
          <div className="h-24 justify-center p-2 rounded border border-gray-300 !hidden md:!inline-flex">
            <img
              src={`/product/image/tile?productId=${state.offer.Id}`}
              alt={state.offer.Name}
              className="max-h-full"
            />
          </div>
          <div className="text-2xl font-bold my-2">{`${state.offer.Name} $${state.selectedOfferItem.Amount}`}</div>
          {Object.keys(state.requiredFields).map(a => (
            <div
              key={a}
              className="flex flex-col flex-wrap md:flex-row md:w-2/3 border border-gray-300 p-4 mx-auto my-4 rounded-xl text-xs relative"
            >
              <span className="absolute bg-white z-10 left-5 -top-2 px-2 text-gray-300 capitalize">
                {a === 'recipient' && RecipientInfoTitle}
                {a === 'sender' && SenderInfoLabel}
                {a === 'delivery' && DeliveryInfoTitle}
              </span>
              {state.requiredFields[a].map(f => (
                <ReceiptFields
                  area={a}
                  field={f}
                  formData={state.formData}
                  mask={state.mask}
                  key={f.id}
                  carrier={state.carrier}
                />
              ))}
            </div>
          ))}
          <div className="flex flex-col md:flex-row md:w-2/3 border border-gray-300 p-4 mx-auto my-4 rounded-xl text-xs">
            <div className="flex md:flex-col items-start basis-1/3">
              <span className="mr-2 mb-1 md:mb-0 md:mr-0 after:content-[':']">{AmountLabel}</span>
              <span>${state.selectedOfferItem.Amount}</span>
            </div>
            {Number(state.selectedOfferItem.LocalAmount) >= 0 && (
              <div className="flex md:flex-col items-start basis-1/3">
                <span className="mr-2 mb-1 md:mb-0 md:mr-0 after:content-[':']">{LocalAmountLabel}</span>
                <span>
                  {Number(state.selectedOfferItem.LocalAmount) / 100} {state.selectedOfferItem.LocalCurrency}
                </span>
              </div>
            )}
            {Number(state.selectedOfferItem.TransactionFee) >= 0 && (
              <div className="flex md:flex-col items-start basis-1/3">
                <span className="mr-2 mb-1 md:mb-0 md:mr-0 after:content-[':']">{TransactionFeeLabel}</span>
                <span>${state.selectedOfferItem.TransactionFee}</span>
              </div>
            )}
          </div>
          <div className="flex md:w-2/3 justify-between mx-auto px-4 py-2 bg-black/[.05] text-2xl font-bold mb-2">
            <span>Total</span>
            <span>${Number(state.selectedOfferItem.Amount) + Number(state.selectedOfferItem.TransactionFee || 0)}</span>
          </div>
        </div>
      </Island>
      <div className="flex flex-col md:flex-row justify-between -mt-2" id="receiptActions">
        <div className="mb-2 md:mb-0">
          <Link to="/vouchers/index" style={{ textDecoration: 'none' }}>
            <Button type="button" intent="primary" className="w-full">
              {StartNewOrderBtn}
            </Button>
          </Link>
        </div>
        <div className="flex">
          <Button type="button" intent="secondary" outline className="mr-2 grow" onClick={handlePrint}>
            {PrintBtnTitle}
          </Button>
          <Button type="button" intent="secondary" outline className="grow" onClick={handlePrint}>
            {SmallPrintBtnTitle}
          </Button>
        </div>
      </div>
      <Toast
        content={toastMessage?.content || ''}
        open={toastVisible}
        setOpen={setToastVisible}
        title={toastMessage?.title}
        intent={toastMessage?.intent || 'info'}
        duration={toastMessage?.duration}
      />
    </>
  );
}
